
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Group from 'vux/src/components/group/index.vue'

import FileItem from 'c%/FileItem.vue'


export default{
mixins: [ common, globals ],
components: {
Group,
FileItem
},
data () {
return {
info: {
eid: 0,
},
loading: false,
id: this.$route.params.id,
list: []
}
},
mounted () {
this.init_data()
},
computed: {
level_img () {
let [standard_level,homework,img] = [{1:'A',2:'B',3:'C',4:'D'},this.info.homework_complete,'/assets/images/A.png']
let normal_level = {1:'A+',2:'A',3:'A-',4:'B+',5:'B',6:'B-',7:'C+',8:'C',9:'C-',10:'D'}
if(homework.is_check===1){
if(homework.check_level===2){
img = '/static/img/neza/org/'+normal_level[homework.result_level]+'.png'
}else{
img = '/static/img/neza/org/'+standard_level[homework.result_level]+'.png'
}
}
return img
},
},
methods: {
formatDate (time) {
time = time||''
let result = time.substring(5,10)
return result
},
submit () {
if(!this.info.homework_complete){
this.$router.push({path:'./'+this.id+'/submit',params:{id:this.id}})
}
},
init_data () {
let id = this.id

this.$rest('homework_tasks/'+id).get()
.success(data=>{
this.info = data
this.loading = true
}).error(response=>{
this.loading = true
this.toast(response.body.message||'加载错误')
})
}
}
}



